.select-wrapp {
  width: 180px;
  position: relative;
  padding-right: 10px;

  [class*="control"] {
    &:hover {
      border: none;
    }
    box-shadow: none;

    div[calss*="placeholder"] {
      font-family: Ubuntu;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
    }
  }
  [class*="indicatorContainer"] {
    position: relative;
    background: url(http://astra.site4u.top/assets/img/select-grey-icon.svg)
      no-repeat #fff;
    background-position: center;
    background-position-y: 5px;
    svg {
      display: none;
    }
  }
}
