@import "./../../scss/varibals";

.ModalContainerWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  .modal-dialog-wrapp {
    background: $bg_litegrey;
    box-shadow: 0px 2px 10px rgba(22, 84, 152, 0.1);
    border-radius: 4px;
    width: 1090px;

    .lists-wrapp {
      display: flex;
      background: #f2f3f8;

      .left-list,
      .right-list {
        padding: 16px;
        padding-right: 0;
        width: 50%;
        padding-bottom: 38px;

        .Search-wrapp,
        .list-header,
        .ItemDetils-wrapp {
          margin-right: 16px;
        }
        .items-box {
          &:nth-child(3) {
            width: 4px !important;
          }
        }
      }

      .right-list {
        border-left: 0.5px solid #dbdce7;
      }
    }
  }
}
